:root {
  font-size: 10px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  box-sizing: border-box;
  --dark-1: #000;
  --dark-2: rgba(0, 0, 0, 0.65);
  --dark-3: rgba(0, 0, 0, 0.48);
}

body {
  background-color: rgb(248, 248, 248);
}

p,
a,
span {
  font-size: 1.6rem;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 14px 24px;
}
.header .brand-logo-container {
  font-weight: 600;
}
.header .brand-logo {
  font-size: 3rem;
}
.header .search-container {
  display: flex;
  width: 48px;
  height: 48px;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 100%;
}

main {
  display: flex;
  flex-direction: column;
  margin-top: 66px;
  padding: 0 42px;
}

.hero-banner {
  margin-bottom: 10rem;
}
.hero-banner h1 {
  font-size: 5.8rem;
  margin: 0 0 2rem 0;
}
.hero-banner span {
  font-size: 1.8rem;
  color: var(--dark-2);
}

.categories h2 {
  margin: 0 0 2.4rem 0;
  font-size: 3.2rem;
}
.categories .category-cards-container {
  display: flex;
  flex-direction: row;
  gap: 24px;
  overflow-x: scroll;
}
.categories .category-cards-container .category-card {
  display: flex;
  flex-direction: column;
  width: 196px;
  height: 240px;
  background-color: rgb(255, 255, 255);
  border-radius: 12px;
  margin-bottom: 30rem;
}
.categories .category-cards-container .category-card img {
  width: 196px;
  height: 120px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}
.categories .category-cards-container .category-card span {
  padding: 24px;
  font-size: 2.4rem;
  font-weight: 500;
}